import React from 'react';
import {FormGroup,FormControl, InputGroup, Button} from 'react-bootstrap';
import '../App.css';


class  CurrencyInput extends React.Component{

    constructor(props){

        super(props);


    }

    componentDidMount (){

    }

    render() {

        return (

            <FormGroup bsSize="large">
                <InputGroup>
                    <InputGroup.Addon>
                        1 USD
                    </InputGroup.Addon>
                    <FormControl type="number"  onChange={this.handleAmountChange} value={this.props.USDARS}/>
                    <InputGroup.Addon >
                        ARS
                    </InputGroup.Addon>
                    <InputGroup.Button>
                        <Button bsSize="large" onClick={this.handleValueSave}>
                            <i className="fa fa-floppy-o" aria-hidden="true"></i>
                        </Button>
                    </InputGroup.Button>
                </InputGroup>
            </FormGroup>
        );
    }

    handleAmountChange = (event) => {
        let USDARS = event.target.value;
        this.props.handleValueChange(USDARS);
    }

    handleValueSave = () => {
        this.props.handleValueSave(this.props.USDARS);
    }



}


export default CurrencyInput;



